/* load container */
div.load-container {
  display: none;
  z-index: 9000;
  padding: 20px 0 20px 0;
  text-align: center;
}
div.load-container p {
  display: inline-block;
  position: relative;
  padding-left: 30px;
  font-size: 16px;
  font-weight: 700;
  color: #000;
}
div.load-container div.loader {
  display: inline-block;
  font-size: 30px;
  overflow: hidden;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  margin: auto;
  position: relative;
  animation: loading 1.5s infinite ease;
  -webkit-animation: loading 1.5s infinite ease;
}
#intro .researcher-head {
  border-bottom: 0;
  display: inline-block;
  margin: 0 0 1em 0;
  overflow: hidden;
  position: relative;
  width: 8em;
}
